<template>
  <demo title="Input" class="input-demo">
    <demo-item name="size" description="" :code="code">
      <Input :size="'sm'" v-model="size_test"></Input>
      <Input :size="'md'" v-model="size_test"></Input>
      <Input :size="'lg'" v-model="size_test"></Input>
    </demo-item>
    <demo-item name="labelFloat" description="" :code="label_code">
      <Input :label="''" v-model="label_test"></Input>
      <Input :placeholder="''" v-model="label_test"></Input>
    </demo-item>
    <demo-item name="status" description="" :code="disabled_code">
      <Input :placeholder="''" v-model="status_test"></Input>
      <Input :placeholder="''" :disabled="true"></Input>
    </demo-item>
    <demo-item name="color" description="" :code="color_code">
      <Input :placeholder="''" v-model="color_test"></Input>
      <Input :placeholder="''" color="#4fae9B" v-model="color_test"></Input>
      <Input :placeholder="''" color="rgb(0, 160, 233)" v-model="color_test"></Input>
    </demo-item>
    <demo-item name="verify" description="" :code="verify_code">
      <Input :placeholder="'(7~14)'" verify="phone" @verify="verify_result = arguments[0]"></Input>
      <p>: {{ verify_result }}</p>
      <Input :placeholder="'/'" :verify="['phone', 'email']" @verify="verify3_result = arguments[0]"></Input>
      <p>/: {{ verify3_result }}</p>
      <Input :placeholder="' '" :reg="//" tip="" @verify="verify1_result = arguments[0]"></Input>
      <p>1{{verify1_result}}</p>
      <Input :placeholder="'2'" :ref="'verify_input'" @verify="verify2_result = arguments[0]"></Input>
      <p>
        <Btn @click="$refs.verify_input.$emit(verify2_result ? 'error' : 'correct', '')">{{ verify2_result ? '' : '' }}</Btn>
        <Btn @click="$refs.verify_input.$emit('clear')">(false)</Btn>
      </p>
      <p>1{{verify2_result}}</p>
    </demo-item>
    <demo-item name="textarea" description="" :code="textarea_code">
      <Input v-model="textarea_test"></Input>
      <Input v-model="textarea_test" :placeholder="'3'" type="textarea"></Input>
      <Input v-model="textarea_test" type="textarea" :placeholder="''" :autosize="true"></Input>
    </demo-item>
    <demo-item name="select" description="" :code="textarea_code">
      <Input v-model="select_test" :type="'select'" :enums="['', '', '']" :label="''"></Input>
    </demo-item>
  </demo>
</template>

<style lang="stylus" scoped>
  @import '../../style/';
  .hiui-input{ margin-bottom:5px }
  .verify-result { margin-top:5px;margin-bottom:5px }
</style>

<script>
  export default {
    data () {
      return {
        size_test: '',
        label_test: '',
        status_test: '',
        verify_test: '',
        verify1_test: '',
        verify2_test: '',
        color_test: '',
        textarea_test: '',
        select_test: '',
        verify_result: false,
        verify1_result: false,
        verify2_result: false,
        verify3_result: false,
        code: `
<Input :size="'sm'" v-model="test"></Input>
<Input :size="'md'" v-model="test"></Input>
<Input :size="'lg'" v-model="test"></Input>
        `,
        label_code: `
// label
<Input :label="''" v-model="label_test"></Input>
<Input :placeholder="''" v-model="label_test"></Input>
        `,
        color_code: `
<Input :placeholder="''" v-model="status_test"></Input>
<Input :placeholder="''" color="#4fae9B" v-model="color_test"></Input>
<Input :placeholder="''" color="rgb(0, 160, 233)" v-model="color_test"></Input>
        `,
        disabled_code: `
<Input :placeholder="''" v-model="status_test"></Input>
<Input :placeholder="''" :disabled="true" v-model="status_test"></Input>
        `,
        verify_code: `
// API
<Input :placeholder="'(7~14)'" verify="phone" @verify="verify_result = arguments[0]"></Input>
<p>: {{ verify_result }}</p>
<Input :placeholder="'/'" :verify="['phone', 'email']" @verify="verify3_result = arguments[0]"></Input>
<p>/: {{ verify3_result }}</p>
<Input :placeholder="' '" :reg="//" tip="" @verify="verify1_result = arguments[0]"></Input>
<p>1{{verify1_result}}</p>
<Input :placeholder="'2'" :ref="'verify_input'" @verify="verify2_result = arguments[0]"></Input>
<p>
  <Btn @click="$refs.verify_input.$emit(verify2_result ? 'error' : 'correct', '')">{{ verify2_result ? '' : '' }}</Btn>
  <Btn @click="$refs.verify_input.$emit('clear')">(false)</Btn>
</p>
<p>1{{verify2_result}}</p>
        `,
        textarea_code: `
<Input v-model="textarea_test"></Input>
<Input v-model="textarea_test" :placeholder="'3'" type="textarea"></Input>
<Input v-model="textarea_test" type="textarea" :placeholder="''" :autosize="true"></Input>
        `
      }
    }
  }
</script>



